import {UserModel} from "../../model/user";
const  userModel = new UserModel()
import noData from "../../components/noData/noData"
export default {
  data() {
    return {
        cur_hd:1,
        list1:[],
        list2:[]
    };
  },
  methods: {
      clickTap(val){
          this.cur_hd=val
          this.getAddress()
      },
      toAddTxAddress(){
          uni.navigateTo({
              url:'/pagesA/my_address_addtx/my_address_addtx'
          })
      },
      toAddTcAddress(){
          uni.navigateTo({
              url:'/pagesA/my_address_addtc/my_address_addtc'
          })
      },
      getAddress()
      {
          console.log(this.cur_hd)
          let params = {
              type : this.cur_hd
          }
          userModel.getAddress(params).then((res) => {
              switch(this.cur_hd){
                  case 1:
                      this.list1 = res.data
                      break;
                  case 2:
                      this.list2 = res.data
                      break;
              }
          })
      },
      toTxDetails(id){
          uni.navigateTo({
              url:`/pagesA/my_address_addtx/my_address_addtx?id=${id}`
          })
      },
      toTcDetails(id){
          uni.navigateTo({
              url:`/pagesA/my_address_addtc/my_address_addtc?id=${id}`
          })
      },
  },
  components: {
      noData
  },
  onLoad() {
      
  },
  onShow() {
      this.getAddress()
  },
  onHide() {}
};
